/* Styles for the periodic table container */
.periodic-table {
    display: grid;
    grid-gap: 8px;
    grid-template-columns: repeat(18, 52px);
    grid-template-rows: repeat(9, 56px);
}

/* Styles for individual elements */
.element {
    position: relative;
    background: #D3E7FB;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
    cursor: pointer;
}


.element:hover {
    background: #3b45e5;
}


.element:hover .symbol,
.element:hover .element-atomic-number {
    color: white;
}


/* Styles for element symbol */
.symbol {
    position: absolute;
    right: 0;
    bottom: 5px;
    left: 0;
    font-size: 24px;
    color: #020c1a;
    line-height: 32px;
    font-weight: 700;
}

/* Styles for element name */
.name {
    font-size: 0.8em;
}

.element-atomic-number {
    position: absolute;
    top: 5px;
    left: 6px;
    display: block;
    font-size: 12px;
    line-height: 18px;
}

.h {
    background: linear-gradient(288deg, #F1E7A3 -5%, #FAF4CA 105%);
}

.principal-block {
    background: linear-gradient(288deg, #C8F3AC -5%, #DFFDCA 105%);
}

.principal2-block {
    background: linear-gradient(288deg, #F1E7A3 -5%, #FAF4CA 105%);
}

.principal3-block {
    background: linear-gradient(288deg, #BFF4CF -5%, #D9FFE5 105%);
}

.diffuse-block {
    background: linear-gradient(288deg, #BAD3FF -2%, #D9EAFF 105%);
}

.sharp-block {
    background: linear-gradient(288deg, #FFBFB6 -4%, #FFD2CE 104%);
}

.sharp2-block {
    background: linear-gradient(288deg, #DDBAFF -5%, #E6D3FF 103%);
}

.la-block {
    background: linear-gradient(288deg, #B6EFFF -2%, #D3F1FF 103%);
}

.ac-block {
    background: linear-gradient(288deg, #BFF9E6 -2%, #DCFFF6 103%);
}

.inert-block {
    background: linear-gradient(288deg, #FCDEA3 0%, #FCEECC 102%);
}

.selected {
    background: #3b45e5;
}

.selected:hover {
    background: #3b45e5;
}

.selected:hover .symbol,
.selected:hover .element-atomic-number {
    color: #fff;
}

.element-atomic-number {
    color: #FFF;
}

.number-container {
    position: absolute;
    margin-top: 64px;
    margin-left: 120px;
}

.number-container .second-line {
    margin-left: 120px;
}

.number-keyboard {
    display: flex;
    column-gap: 8px;
}

.keyboard-key {
    color: #353966;
    background-color: #fff;
    font-family: Roboto;
    font-size: 23px;
    font-weight: 700;
    border: 1px solid transparent;
    border-radius: 4px;
    box-sizing: border-box;
    cursor: pointer;
    width: 52px;
    height: 56px;
}


.keyboard-key:hover {
    color: #fff;
    background-color: #6063F0;
}